import axios from 'axios';
import { message, Modal } from 'antd';

// 401
let isShowingAuthModal = false;

//  axios 
const http = axios.create({
    baseURL: process.env.REACT_APP_BASE_URL + '/dhapi', // API URL
    timeout: 60000 * 10, // 
    headers: {
        'Content-Type': 'application/json',
    }
});

// 
http.interceptors.request.use(
    (config) => {
        const token = localStorage.getItem('token');
        let noAuth = true;
        
        // URLAuthGuard.jsx
        const whiteListUrls = ['/auth/', '/invitation/', '/wx/', '/landing/', '/membership/', '/video-edit', '/no-permission'];
        
        // URLtoken
        if (whiteListUrls.some(url => config.url.includes(url))) {
            noAuth = false;
        }
        
        // 401
        if (isShowingAuthModal) {
            return Promise.reject('');
        }
        
        if (!token && noAuth) {
            message.error(',');
            window.location.href = '/login';
            return Promise.reject('');
        }
        
        config.headers.Authorization = `Bearer ${token}`;
        return config;
    },
    (error) => {
        return Promise.reject(error);
    }
);

// 
http.interceptors.response.use(
    (response) => {
        // 
        return response.data;
    },
    (error) => {
        if (error.response) {
            switch (error.response.status) {
                case 401: // 
                    // 
                    if (isShowingAuthModal) {
                        return Promise.reject(error);
                    }
                    
                    // 
                    isShowingAuthModal = true;

                    // Modal.confirm
                    Modal.confirm({
                        title: '',
                        content: error?.response?.data?.detail || '',
                        okText: '',
                        cancelText: '',
                        onOk: () => {
                            // token
                            localStorage.removeItem('token');
                            localStorage.removeItem('userInfo');
                            window.location.href = '/login';
                        },
                        afterClose: () => {
                            // 
                            isShowingAuthModal = false;
                        }
                    });
                    break;
                case 403: // 
                    message.error('');
                    break;
                case 404: // 
                    message.error('');
                    break;
                case 500: // 
                    message.error('');
                    break;
                default:
                    message.error(error?.response?.data?.msg || error?.response?.data?.detail);
            }
        } else if (error.request) {
            // 
            message.error('');
        } else {
            // 
            message.error(': ' + error.message);
        }
        return Promise.reject(error);
    }
);

//  GET 
export const get = (url, params) => {
    return http.get(url, { params });
};

//  POST 
export const post = (url, data) => {
    return http.post(url, data);
};

//  PUT 
export const put = (url, data) => {
    return http.put(url, data);
};

//  DELETE 
export const del = (url) => {
    return http.delete(url);
};

export default http;
