import http from "@/utils/http";

// 
export enum WithdrawalStatus {
  PENDING = 'pending',
  COMPLETED = 'completed',
  REJECTED = 'rejected'
}

// 
export interface CommissionInfo {
  total_commission: number;      // 
  available_amount: number;      // 
  withdrawn_amount: number;      // 
  pending_withdrawal: number;    // 
  recent_withdrawals: {
    id: string;
    amount: number;
    status: WithdrawalStatus;
    created_at: string;
    remark: string;
  }[];
}

export const getUserInfo = () => {
  return http.get("/user/profile");
};

// 
export const getCommissionInfo = () => {
  return http.get<{
    code: number;
    msg: string;
    data: CommissionInfo;
  }>("/user/commission_info");
};

// 
export const applyWithdrawal = (amount: number) => {
  return http.post<{
    code: number;
    msg: string;
    data: {
      id: string;
      amount: number;
      status: WithdrawalStatus;
      created_at: string;
    };
  }>("/withdrawal/apply", { amount });
};

export const uploadAvatar = (file) => {
  const formData = new FormData();
  formData.append('file', file);
  return http.post('/user/upload_avatar', formData, {
      headers: {
          'Content-Type': 'multipart/form-data'
      }
  });
};

export async function updateNickname(data: {
  nickname?: string;
}) {
  return http.post<{
    code: number;
    msg: string;
  }>('/user/update_nickname', data);
}

export default {
  getUserInfo,
  getCommissionInfo,
  applyWithdrawal,
  uploadAvatar,
  updateNickname
};
